.background-container-catalog {
  background-image: url("../img/index-background.jpg");
  background-color: #3f5e72;
  background-repeat: no-repeat;
  width: 320px;
  margin: 0 auto;
  background-position: bottom right;
  height: 157px;
  margin-top: 13px;
  margin-bottom: 1px;

  ::after {
    display: block;
    content: "";
    background-image: url("../img/intro-triangle-desktop-catalog.svg");
    width: 320px;
    height: 57px;
    top: 5px;
    bottom: 96px;
    left: -47px;
    background-position: center;
    background-repeat: no-repeat;
    position: relative;
    box-sizing: border-box;
    margin: 0 auto;
  }
}

@media (min-resolution: 2dppx) {
  .background-container-catalog {
    background-image: url("../img/index-background@2x.jpg");
    background-size: 320px auto;
  }
}

@media (min-width: $tablet-width) {
  .background-container-catalog {
    background-image: url("../img/back-tablet-catalog-form.jpg");
    width: 768px;
    margin: 0 auto;
    height: 390px;
    margin-bottom: 28px;

    ::after {
      display: block;
      content: "";
      background-image: url("../img/intro-triangle-desktop-catalog.svg");
      width: 768px;
      height: 57px;
      top: 58px;
      bottom: 96px;
      left: -155px;
      background-position: center;
      background-repeat: no-repeat;
      position: relative;
      box-sizing: border-box;
      margin: 0 auto;
    }
  }
}

@media (min-width: $tablet-width) and (min-resolution: 2dppx) {
  .background-container-catalog {
    background-image: url("../img/back-tablet-catalog-form@2x.jpg");
    background-position: bottom center, center top;
    width: 768px;
    background-size: cover;
  }
}

@media (min-width: $desktop-width) {
  .background-container-catalog {
    background-image: url("../img/back-desktop-catalog-form.jpg");
    width: 1200px;
    height: 402px;
    margin-bottom: 61px;

    ::after {
      display: block;
      content: "";
      background-image: url("../img/intro-triangle-desktop-catalog.svg");
      width: 1200px;
      height: 57px;
      top: 60px;
      bottom: 96px;
      left: -368px;
      background-position: center;
      background-repeat: no-repeat;
      position: relative;
      box-sizing: border-box;
      margin: 0 auto;
    }
  }
}

@media (min-width: $desktop-width) and (min-resolution: 2dppx) {
  .background-container-catalog {
    background-image: url("../img/back-desktop-catalog-form@2x.jpg");
    background-position: bottom center, center top;
    width: 1200px;
    background-size: cover;
  }
}

@media (min-width: $tablet-width) {
  .background-container-form {
    margin-bottom: 64px;
  }
}

@media (min-width: $desktop-width) {
  .background-container-form {
    margin-bottom: 116px;
  }
}
