.main-nav {
  background-color: #81b3d2;
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  width: auto;
  margin: -54px auto;
  min-height: 55px;
  position: relative;
}
@media (min-width: 768px) {
