.control {
  position: relative;
  display: block;
  padding-left: 42px;
  cursor: pointer;
  list-style: none;
}

.control__label {
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", "Helvetica", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  padding-bottom: 16px;
}

.control__mark {
  position: absolute;
  top: -10px;
  left: -18px;
  width: 46px;
  height: 46px;
  border-radius: 7px;
  background-color: #f2f2f2;
}

.control__input[type=radio]:checked + .control__mark::before {
  position: absolute;
  top: 15px;
  left: 14px;
  width: 16px;
  height: 16px;
  background-color: #000000;
  border-radius: 50%;
  content: "";
}

.control__input[type=radio] + .control__mark {
  border-radius: 50%;
  background-image: none;
  box-sizing: border-box;
  cursor: pointer;
}

.control__input[type=radio]:active + .control__mark {
  background-repeat: no-repeat;
  position: absolute;
  opacity: 30%;
  padding: 0 auto;
  background-position: center;
  color: black;
  background-color: #f2f2f2;
}

.control__input[type=radio] + .control__mark:active {
  background-repeat: no-repeat;
  position: absolute;
  padding: 0 auto;
  background-position: center;
  color: black;
  background-color: #d5d5d5;
}

.control__mark:hover {
  background-color: #e5e5e5;
}

.control__input {
  margin-right: 19px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

