.social__list {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  padding: 36px 81px;
  justify-content: space-between;
  padding-bottom: 20px;
  position: relative;

  @media (min-width: $tablet-width) {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-top: 14px;
    padding-bottom: 5px;
    width: 156px;
    padding-left: 109px;
  }
}

@media (min-width: $desktop-width) {
  .social__list {
    padding-left: 112px;
  }
}

.social__link {
  display: block;
  color: #ffffff;
  text-decoration: none;
}

.social__item {
  width: 48px;
  height: 48px;
  margin-right: 0;
  position: relative;
  background-color: $accent;
  padding: 12px;
  box-sizing: border-box;
  cursor: pointer;

  @media (min-width: $tablet-width) {
    width: 48px;
    height: 48px;
    position: relative;
    background-color: #81b3d2;
    padding: 12px;
    box-sizing: border-box;
  }
}

.social__item:hover {
  width: 48px;
  height: 48px;
  margin-right: 0;
  position: relative;
  background-color: rgb(102, 158, 192);
  padding: 12px;
  box-sizing: border-box;
}

.social__item:active {
  width: 48px;
  height: 48px;
  margin-right: 0;
  position: relative;
  background-color: rgb(84, 150, 189);
  padding: 12px;
  box-sizing: border-box;
  color: rgb(255, 255, 255, 0.3);
}

.main-footer__link {
  text-decoration: none;
}

.social__item:first-child::before {
  content: "";
  position: absolute;
  display: block;
  background-image: url("../img/vk.svg");
  width: 32px;
  height: 74px;
  left: 7px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 24px;
  top: -14px;
}

.social__item:nth-child(2)::after {
  content: "";
  position: absolute;
  display: block;
  background-image: url("../img/telegram.svg");
  width: 32px;
  height: 74px;
  left: 7px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 24px;
  top: -14px;
}

.social__item:nth-child(3)::after {
  content: "";
  position: absolute;
  display: block;
  background-image: url("../img/youtube.svg");
  width: 32px;
  height: 74px;
  left: 7px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 23px;
  top: -13px;
}

.social__item:active::after {
  opacity: 0.3;
}

.social__item:active::before {
  opacity: 0.3;
}
